import { useEffect, useState } from "react";
import { Link as ScrollLink, Events, scrollSpy } from "react-scroll";
import { FaEnvelope, FaPhone } from "react-icons/fa";
import Image from "next/image";
import Link from "next/link";

export default function Header() {
  const [activeSection, setActiveSection] = useState("home");

  useEffect(() => {
    scrollSpy.update();
    return () => {};
  }, []);

  const navItems = [
    { id: "home", label: "Domov" },          // HERO
    { id: "about", label: "O n치s" },
    { id: "partners", label: "Ofici치lne zast칰penia" },
    { id: "contact", label: "Kontakt" },
  ];

  return (
    <header className="fixed top-0 left-0 w-full bg-white shadow-md z-50">
      <div className="max-w-[1200px] mx-auto px-6 h-[72px] flex items-center justify-between">
        
        {/* LOGO */}
        <Link href="/" className="flex items-center">
          <Image
            src="/logo_header.png"
            alt="ELMORA"
            width={200}     // 游녣 TU zv칛캜코칤코 logo
            height={60}
            priority
          />
        </Link>

        {/* NAVIG츼CIA */}
        <nav className="flex items-center gap-6">
          {navItems.map((item) => (
            <ScrollLink
              key={item.id}
              to={item.id}
              spy={true}
              smooth={true}
              duration={600}
              offset={-90} // kv칪li fixed headeru
              onSetActive={() => setActiveSection(item.id)}
              className={`cursor-pointer font-medium px-3 py-2 rounded-md transition
                ${
                  activeSection === item.id
                    ? "bg-green-600 text-white"
                    : "text-gray-700 hover:text-green-700"
                }`}
            >
              {item.label}
            </ScrollLink>
          ))}

          {/* IKONY */}
          <a
            href="mailto:info@elmoraslovakia.sk"
            className="text-gray-600 hover:text-green-700 ml-2"
            title="Nap칤sa콘 e-mail"
          >
            <FaEnvelope size={18} />
          </a>

          <a
            href="tel:+421912345678"
            className="text-gray-600 hover:text-green-700"
            title="Zavola콘"
          >
            <FaPhone size={18} />
          </a>
        </nav>
      </div>
    </header>
  );
}
